"use strict";

var gtxTool = require('./gtx');

var secp256k1 = require('secp256k1');

var util = require('../util');

exports.createClient = function (restApiClient, blockchainRID, functionNames) {
  functionNames.push('message');

  function transaction(gtx) {
    return {
      gtx: gtx,
      sign: function sign(privKey, pubKey) {
        var pub = pubKey;

        if (!pub) {
          pub = secp256k1.publicKeyCreate(privKey);
        }

        gtxTool.sign(privKey, pub, this.gtx);
      },
      getTxRID: function getTxRID() {
        return this.getDigestToSign();
      },
      getDigestToSign: function getDigestToSign() {
        return gtxTool.getDigestToSign(this.gtx);
      },
      addSignature: function addSignature(pubKey, signature) {
        gtxTool.addSignature(pubKey, signature, this.gtx);
      },
      // raw call
      addOperation: function addOperation(name) {
        for (var _len = arguments.length, args = new Array(_len > 1 ? _len - 1 : 0), _key = 1; _key < _len; _key++) {
          args[_key - 1] = arguments[_key];
        }

        gtxTool.addTransactionToGtx(name, args, this.gtx);
      },
      postAndWaitConfirmation: function postAndWaitConfirmation() {
        return restApiClient.postAndWaitConfirmation(gtxTool.serialize(this.gtx), this.getTxRID());
      },
      send: function send(callback) {
        var gtxBytes = gtxTool.serialize(this.gtx);
        restApiClient.postTransaction(gtxBytes, callback);
        this.gtx = null;
        this.gtxBytes = gtxBytes;
      },
      encode: function encode() {
        return gtxTool.serialize(this.gtx);
      }
    };
  }

  function addFunctions(req) {
    functionNames.forEach(function (functionName) {
      req[functionName] = function () {
        for (var _len2 = arguments.length, args = new Array(_len2), _key2 = 0; _key2 < _len2; _key2++) {
          args[_key2] = arguments[_key2];
        }

        gtxTool.addTransactionToGtx(functionName, args, this.gtx);
      };
    });
  }

  var client = {
    newTransaction: function newTransaction(signers) {
      var newGtx = gtxTool.emptyGtx(blockchainRID);
      signers.forEach(function (signer) {
        return gtxTool.addSignerToGtx(signer, newGtx);
      });
      var req = transaction(newGtx);
      addFunctions(req);
      return req;
    },
    transactionFromRawTransaction: function transactionFromRawTransaction(rawTransaction) {
      var gtx = gtxTool.deserialize(rawTransaction);
      var req = transaction(gtx);
      addFunctions(req);
      return req;
    },

    /*
    This function is only used for the messaging feature. It's a convenience function that
    uses POST /query under the hood.
     */
    pollMessages: function pollMessages(recipient, fromMessageId, maxHits, resultCallback) {
      var queryObject = {
        type: 'getGtxMessages',
        recipient: recipient.toString('hex'),
        sinceMessageId: fromMessageId == null ? null : fromMessageId.toString('hex'),
        maxHits: maxHits
      };
      restApiClient.query(queryObject, function (error, messages) {
        if (error) {
          resultCallback(error);
          return;
        }

        var result = messages.map(function (gtxAndIndex) {
          console.log("gtxData: " + JSON.stringify(gtxAndIndex.txData));
          console.log("isBuffer: " + Buffer.isBuffer(gtxAndIndex.txData));
          var txData = Buffer.from(gtxAndIndex.txData, 'hex');
          var gtx = gtxTool.deserialize(txData);
          var call = gtx.calls[gtxAndIndex.callIndex]; // call args: recipients, 'testtype', messageBytes

          return {
            recipients: call.args[0],
            type: call.args[1],
            message: call.args[2],
            messageId: util.hash256(txData)
          };
        });
        resultCallback(null, result);
      });
    },
    query: function query(queryName, queryObject) {
      return restApiClient.query(queryName, queryObject);
    }
  };
  return client;
};